.panel-heading {
	overflow: auto;
	font-weight: bold;
	font-size: 16px;
}

.keyAddBtn {
	float: right;
}

.panel-body {
	text-align: center;
}

.chordPanel{
	background-color: white;
	-webkit-transition: background-color .3s;
	-o-transition: background-color .3s;
	transition: background-color .3s;
}

.chordPanel:hover {
	cursor: pointer;
	background-color: #98FB98;
}


